import { Component, OnInit,Input } from '@angular/core';
import { NbDialogService } from '@nebular/theme';
import { ToastrComponent } from '../../../@core/components';
import { FileExportService } from '../../services/app_services/export_file.service';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'ngx-op-data-table4',
  templateUrl: './op-data-table4.component.html',
  styleUrls: ['./op-data-table4.component.scss']
})
export class OpDataTable4Component implements OnInit {
  @Input()  data: any
  @Input()  headers: any
  @Input()  isLoading = false
  @Input()  title = 'Exported Document by Onepay'
  @Input()  description = 'Exported Document by Onepay'

  allHeaders=[]
  
  p: number = 1;
  pageSize: number = 10
  total: number = 0;
  viewModel: any;

  constructor(
    private toastr: ToastrComponent, private FileExportService: FileExportService, private dialogService: NbDialogService,private route: ActivatedRoute,
    private router: Router
  ) { 
    
  }

  ngOnInit(): void {
    if(this.headers.length) {
      this.allHeaders = this.headers
    }
  }
  headCellRender(){
    if(this.headers.length) {
      let selected_headers = this.allHeaders.filter(header => header['fixed'] || header['selected']);
      return selected_headers
    } else {
      return []
    }
  }

  getSelectionCheckStatus(row) {
    return row['fixed'] || row['selected']
  }

  changeSelectionStatus(header, event) {
      console.log(header.key,'cheack table')
    if(event.target.checked) {
      header.selected = true
      this.allHeaders[this.allHeaders.findIndex(el => el.key === header.key)] = header;
    } else {
      header.selected = false
      this.allHeaders[this.allHeaders.findIndex(el => el.key === header.key)] = header;
    }
  }

  openColumnSelection(selectionModel) {
    this.viewModel = this.dialogService.open(selectionModel, {
      closeOnBackdropClick: false,
    });
  }

  cancel() {
    this.viewModel.close()
  }

  renderCells(item, head){
    return item[head.key] ? item[head.key] : 'N/A'
  }

  exportToExcel(){
   
    if(!this.data.length) {
   
      this.toastr.showToast('danger', 'error', 'Oh Snap! - There are not available data to export');
      return;
    }

    this.FileExportService.exportAsExcelFile(this.data, this.allHeaders.filter(header => header['fixed'] || header['selected']), 'onepay_report')
  }

  exportPDF() {
    
    if(!this.data.length) {
      this.toastr.showToast('danger', 'error', 'Oh Snap! - There are not available data to export');
      return;
    }

    this.FileExportService.createPdf(this.data, this.allHeaders, this.title, this.description)
  }
  viewTransaction(id){
    console.log(id,'jhhgjg55')
    this.router.navigate(['/pages/admin-transaction-history', id])
  }


}
