
import { Component, inject,Output, EventEmitter } from '@angular/core';
import { NgbCalendar, NgbDate, NgbDateParserFormatter, NgbDatepickerModule } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule } from '@angular/forms';
import { JsonPipe, CommonModule } from '@angular/common'; 

@Component({
  selector: 'ngbd-datepicker-range-popup',
  standalone: true,
  imports: [NgbDatepickerModule, FormsModule, JsonPipe, CommonModule], 
  templateUrl: './ngbd-datepicker-range-popup.component.html',
  styles: [
    `
      .dp-hidden {
        width: 0;
        margin: 0;
        border: none;
        padding: 0;
      }
      .custom-day {
        text-align: center;
        padding: 0.185rem 0.25rem;
        display: inline-block;
        height: 1.9rem;
        width: 1.9rem;
      }
      .custom-day.focused {
        background-color: #e6e6e6;
        border:1px solid #5A996A;
         border-radius:10px
      }
      .custom-day.range{
       background-color: rgb(190	255	207);
        color: black;
          border-radius:10px
       
      }
      .custom-day:hover {
        background-color: rgb(190	255	207);
        color: black;
        border:1px solid #5A996A;
         border-radius:10px
       
      }
      .custom-day.faded {
        background-color: rgba(190	255	207, 0.5);
         border-radius:10px
      }
    `,
  ],
})
export class NgbdDatepickerRangePopup {
  @Output() dateRangeSelected = new EventEmitter<{ fromDate: NgbDate | null, toDate: NgbDate | null }>();
  calendar = inject(NgbCalendar);
  formatter = inject(NgbDateParserFormatter);

  hoveredDate: NgbDate | null = null;
  fromDate: NgbDate | null = this.calendar.getToday();
  toDate: NgbDate | null = this.calendar.getNext(this.calendar.getToday(), 'd', 10);



  // Method to emit the selected date rannge

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
  }

 
  toNativeDate(ngbDate: NgbDate | null): Date | null {
    if (ngbDate) {
      return new Date(ngbDate.year, ngbDate.month - 1, ngbDate.day); 
    }
    return null;
  }

 
  getFormattedDateRange(): string {
    const fromFormatted = this.fromDate ? this.formatter.format(this.fromDate) : '';
    const toFormatted = this.toDate ? this.formatter.format(this.toDate) : '';
    return `${fromFormatted} to ${toFormatted}`;
  }

  isHovered(date: NgbDate) {
    return (
      this.fromDate &&
      !this.toDate &&
      this.hoveredDate &&
      date.after(this.fromDate) &&
      date.before(this.hoveredDate)
    );
  }

  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return (
      date.equals(this.fromDate) ||
      (this.toDate && date.equals(this.toDate)) ||
      this.isInside(date) ||
      this.isHovered(date)
    );
  }

  validateInput(currentValue: NgbDate | null, input: string): NgbDate | null {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }


  
}




